*{
    margin:0px;
    padding: 0px;
}
body{
    background-color: black;
    color:green;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.search-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;

}
.search-container #display{
   text-align: center;
}
#form-container{
    display: flex;
    flex-direction: column;
    margin-top:5px;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
    padding:10px;
}
body input{
    width:200px;
    height:25px;
    text-align: center;
    margin-top: 7px;
    font-size: 17px;
    border-radius: 2px;
    border: 3px solid green;
}
body button{
    margin-top: 10px;
    width:100px;
    height:25px;
    font-weight:900;
    border-radius: 6px;
    outline: none;
    border: none;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}
body button:hover{
   background-color: lightgreen;
}

.cred-container-inner{
    margin-bottom: 20px;
    padding: 20px;
    border: 1px solid green;
}
#display{
    margin-top: 10px;
}
#LoadOrSave{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
}
#LoadOrSave button{
    margin-right: 10px;
    margin-bottom: 10px;
}
.red{
    color:red;
    
}
body h1{
    font-family: cursive;
}
